import React from 'react'
import { Button, Container, Row } from 'react-bootstrap'
import { useSelector } from 'react-redux'
import CIcon from '@coreui/icons-react'
import { cilPen } from '@coreui/icons'

const Index = () => {
  const user = useSelector((state) => state.user.user)
  return (
    <Container fluid>
      <Row>
        <h1>Role Change Price</h1>
      </Row>
      <div className="row">
        <div className="col-md-10">
          <div className="card">
            <div className="card-header">
              <h3 className="card-title">Roles</h3>
            </div>

            <div className="card-body table-responsive p-0">
              {user && user.role === 'Master Distributor' ? (
                <table className="w-100 ms-3">
                  <thead>
                    <tr>
                      <th>Sn</th>
                      <th>Role</th>
                      <th>Amount</th>
                      <th>Buy</th>
                    </tr>
                  </thead>

                  <tbody>
                    <tr>
                      <td>1</td>
                      <td>Admin</td>
                      <td>10000</td>
                      <td>
                        <Button className="myCustomBtn">Buy</Button>
                      </td>
                    </tr>
                  </tbody>
                </table>
              ) : user && user.role === 'Distributor' ? (
                <table
                  className="w-100 ms-3"
                  style={{
                    overflowX: 'hidden',
                  }}
                >
                  <thead>
                    <tr>
                      <th>Sn</th>
                      <th>Role</th>
                      <th>Amount</th>
                      <th>Buy</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td>1</td>
                      <td>Admin</td>
                      <td>10000</td>
                      <td>
                        <Button className="myCustomBtn">Buy</Button>
                      </td>
                    </tr>
                    <tr>
                      <td>2</td>
                      <td>Master Distributor</td>
                      <td>10000</td>
                      <td>
                        <Button className="myCustomBtn">Buy</Button>
                      </td>
                    </tr>
                  </tbody>
                </table>
              ) : user && user.role === 'Retailer' ? (
                <table className="">
                  <thead>
                    <tr>
                      <th>Sn</th>
                      <th>Role</th>
                      <th>Amount</th>
                      <th>Buy</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td>1</td>
                      <td>Admin</td>
                      <td>10000</td>
                      <td>
                        <Button className="myCustomBtn">Buy</Button>
                      </td>
                    </tr>
                    <tr>
                      <td>2</td>
                      <td>Master Distributor</td>
                      <td>10000</td>
                      <td>
                        <Button className="myCustomBtn">Buy</Button>
                      </td>
                    </tr>
                    <tr>
                      <td>3</td>
                      <td>Distributor</td>
                      <td>10000</td>
                      <td>
                        <Button className="myCustomBtn">Buy</Button>
                      </td>
                    </tr>
                  </tbody>
                </table>
              ) : (
                <div className="ms-5 my-3">
                  <table
                    className="w-100 "
                    style={{
                      overflow: 'hidden',
                    }}
                  >
                    <thead>
                      <tr>
                        <th>SN</th>
                        <th>Role</th>
                        <th>Amount</th>
                        <th>Action</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td>1</td>
                        <td>Admin</td>
                        <td>2999</td>
                        <td>
                          <Button className="myCustomBtn" value={1}>
                            <CIcon icon={cilPen} />
                          </Button>
                        </td>
                      </tr>
                      <tr>
                        <td>2</td>
                        <td>Master Distributor</td>
                        <td>300</td>
                        <td>
                          <Button className="myCustomBtn" value={1}>
                            <CIcon icon={cilPen} />
                          </Button>
                        </td>
                      </tr>
                      <tr>
                        <td>3</td>
                        <td>Retailer</td>
                        <td>51</td>
                        <td>
                          <Button className="myCustomBtn" value={1}>
                            <CIcon icon={cilPen} />
                          </Button>
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </Container>
  )
}

export default Index

// ID           Role      Password
// --------------------------------
// ADI-567890   MD      password
// ADI-523690   D       password
// ADI-745896   R       password
// ADI-543210   Ad      password 
